*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
.div-form{
  width: 100%;
  max-width: 600px;
  margin: auto;
}
form {
    width: 100%;
    max-width: 600px;
    padding: 20px;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  form div {
    margin-bottom: 25px;
    width: 100%;
  }
  
  form label {
    display: block;
    font-weight: bold;
    margin-bottom: 5px;
  }
  
  form input {
    width: 100%;
    padding: 18px;
    border: 1px solid #ddd;
    border-radius: 36px;
    background-color: #f0f0f0;
  }
  
  form button {
    background-color: #19a730; /* tomato color */
    color: white;
    border: none;
    padding: 10px 20px;
    border-radius: 36px;
    cursor: pointer;
    font-size: 16px;
    width: fit-content;
  }
  
  form button:hover {
    background-color: #ff4500; /* darker tomato color */
  }
  
  .message {
    margin-top: 20px;
    font-size: 16px;
    color: green;
  }

  .modal {
    position: absolute;
    top: 50%;
    left: 50%;
    right: auto;
    bottom: auto;
    transform: translate(-50%, -50%);
    background-color: #fff;
    padding: 20px;
    border-radius: 10px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.25);
    width: 80%; /* Установите ширину для модального окна */
    max-width: 500px; /* Максимальная ширина для модального окна */
    display: flex;
    align-items: center;
  }
  .modal div{
    width: 100%;
    text-align: center;
  }
  .modal div p{
    margin-top: 20px;
    text-align: center;
  }
  .modal-overlay {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.5);
  }
  
  .close-button {
    position: absolute;
    top: 10px;
    right: 10px;
    background: none;
    border: none;
    font-size: 1.5em;
    cursor: pointer;
  }
  
  @media (max-width: 600px) {
    .modal {
      width: 90%; /* Увеличьте ширину модального окна на мобильных устройствах */
      padding: 10px; /* Уменьшите отступы */
    }
  
    .close-button {
      font-size: 1.2em; /* Уменьшите размер кнопки закрытия */
    }
  
    .modal p {
      font-size: 0.9em; /* Уменьшите размер текста */
    }
  
    .div-form {
      padding: 10px; /* Уменьшите отступы формы */
    } 
  
    .div-form input {
      font-size: 0.9em; /* Уменьшите размер текста ввода */
    }
  
    .div-form button {
      font-size: 0.9em; /* Уменьшите размер текста кнопки */
    }
  }
  