@import url('https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100..900;1,100..900&display=swap');
*{
  font-family: "Montserrat", sans-serif;
}
.back{
  width: 100dvw;
  height: 100dvh;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgba(0, 0, 0, 0.622);
}
.App {
  /* text-align: center; */
  padding: 50px;
  font-family: 'Arial', sans-serif;
  max-width: 600px;
  display: flex;
  flex-direction: column;
  align-items: center;
  color: #fff;
}

h1 {
  font-size: 1.9em;
  margin-bottom: 10px;
  text-align: center;
}
@media screen and (max-width: 600px) {
  h1{
    font-size: 1.7em;
  }
}
p {
  font-size: 1.1em;
  margin-bottom: 30px;
  text-align: center;
}
@media screen and (max-width: 400px) {
  p{
    font-size: 0.9em;
  }
}
.logo{
  width: 200px;
  margin-bottom: 20px;
}